import { NgModule } from '@angular/core';

import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule } from '@angular/router';
import { UiModule } from '@static-pages/shared/ui';
import { UiPaperKitModule } from '@static-pages/shared/ui-paper-kit';
import { CookieLawModule } from 'angular2-cookie-law';
import { AppComponent } from './app.component';

@NgModule({
  declarations: [AppComponent],
  imports: [
    BrowserModule,
    UiModule,
    RouterModule.forRoot(
      [
        {
          path: '',
          pathMatch: 'full',
          loadChildren: () =>
            import('@dundk-friseure/home').then((m) => m.HomeModule),
        },
        {
          path: '',
          loadChildren: () =>
            import('@static-pages/shared/static-pages').then(
              (m) => m.StaticPagesModule
            ),
        },
      ],
      {
        anchorScrolling: 'enabled',
        scrollPositionRestoration: 'enabled',
      }
    ),
    BrowserAnimationsModule,

    CookieLawModule,
    UiModule,
    UiPaperKitModule,
  ],
  providers: [],
  bootstrap: [AppComponent],
})
export class AppModule {}
